import { shipmentslist, cancellation,cancel } from '../API/manjsop.js'
const mixin = {
    data() {
        return {
            columns1,
            columns2,
            columns3,
            columns4,
            datalist: [],
            notyet: [],
        }
    },
    mounted() {
        this.notyet = JSON.parse(this.$route.query.item) || [];

    },
    methods: {
        async notyetlist() {
            let { data } = await shipmentslist(this.notyet.id)
            this.datalist = data
            console.log(this.datalist);

        },
        async canceil() {
            let str = {
                orderId: this.datalist[0].orderId,
                reason: this.qxvalue,
                ofId: this.datalist[0].ofId,
                goodId: this.datalist[0].goodMessage[0].goodId,
                goodsCartId:this.datalist[0].goodMessage[0].goodsCartId
            }
        
            let { data } = await this.$http.cancel(str);
            console.log(data);
        }
    },
}
export default mixin
