<template>
  <!-- 订单详情 -->
  <div class="notyet-body">
    <div class="notyet-header">
      <a-breadcrumb separator=">">
        <a-breadcrumb-item>订单管理</a-breadcrumb-item>
        <a-breadcrumb-item
          ><a href="/management">订单列表</a></a-breadcrumb-item
        >
        <a-breadcrumb-item>{{ this.$route.meta.title }}</a-breadcrumb-item>
      </a-breadcrumb>
      <hr />
      <div>当前订单状态：{{ notyet.order_status }}</div>
    </div>
    <div class="notyet-center">
      <div class="notyet-center-top">
        <div class="notyet-center-left">
          <button>基本信息</button>
        </div>
        <div class="notyet-center-right">
          <button
            @click="showModal"
            v-show="
              notyet.order_status == '待发货' || notyet.order_status == '待付款'
            "
          >
            取消订单
          </button>
        </div>
        <a-modal
          title="取消订单"
          :visible="visible"
          :confirm-loading="confirmLoading"
          @ok="handleOk"
          @cancel="handleCancel"
          okText="提交"
          cancelText="取消"
        >
          取消原因： <input type="text" v-model="qxvalue" />
        </a-modal>
      </div>
      <div class="notyet-center-center">
        <a-table
          :columns="columns1"
          :data-source="datalist"
          bordered
          :pagination="false"
        >
          <template slot="name" slot-scope="text">
            <a>{{ text }}</a>
          </template>
          <template slot="footer"></template>
        </a-table>
      </div>
      <div class="notyet-center-top">
        <div class="notyet-center-left">
          <button>商品信息</button>
        </div>
        <div class="notyet-center-right"></div>
      </div>
      <div class="notyet-center-center">
        <a-table
          :columns="columns2"
          :data-source="datalist"
          bordered
          :pagination="false"
        >
          <template slot="img" slot-scope="text, record">
            <img
              width="70"
              height="70"
              :src="record.goodMessage[0].goodsMainPhoto"
              alt=""
            />
          </template>
          <template slot="footer">合计商品：{{ datalist.length }}件</template>
        </a-table>
      </div>
    </div>
    <div>
      <div class="notyet-center-top">
        <div class="notyet-center-left">
          <button>收货人信息</button>
        </div>
        <div class="notyet-center-right"></div>
      </div>
      <div class="notyet-center-center">
        <a-table
          :columns="columns3"
          :data-source="datalist"
          bordered
          :pagination="false"
        >
          <template slot="name" slot-scope="text">
            <a>{{ text }}</a>
          </template>
        </a-table>
      </div>
    </div>
    <div v-show="notyet.order_status == '售后中'">
      <div class="notyet-center-top">
        <div class="notyet-center-left">
          <button>售后信息</button>
        </div>
        <div class="notyet-center-right"></div>
      </div>
      <div class="notyet-center-center">
        <a-table
          :columns="columns4"
          :data-source="datalist"
          bordered
          :pagination="false"
        >
          <template slot="name" slot-scope="text">
            <a>{{ text }}</a>
          </template>
        </a-table>
      </div>
    </div>
  </div>
</template>

<script>
import mixin from "@/utils/notyet.js";
export default {
  mixins: [mixin],
  data() {
    return {
      visible: false,
      confirmLoading: false,
      qxvalue: "",
    };
  },
  mounted() {
    this.notyetlist();
  },
  methods: {
    showModal() {
      this.visible = true;
    },
    handleOk(e) {
      this.confirmLoading = true;
      setTimeout(() => {
        this.visible = false;
        this.confirmLoading = false;
        this.canceil();
        this.qxvalue = "";
      }, 2000);
    },
    handleCancel(e) {
      this.visible = false;
    },
  },
};
</script>

<style scoped>
.notyet-center-center > :nth-child(1) {
  text-align: right !important;
}
.notyet-header > :nth-child(3) {
  height: 40px;
  line-height: 40px;
  background: #c0c0c0;
  margin-bottom: 10px;
}
.notyet-center-top {
  width: 100%;
  display: flex;
  justify-content: space-around;
}
.notyet-center-left {
  width: 50%;
}
.notyet-center-right {
  width: 50%;
  overflow: hidden;
}
.notyet-center-right button {
  float: right;
}
.notyet-center-center {
  margin-top: 20px;
  margin-bottom: 20px;
}
</style>